.App {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #f0f0f0;
}

body {
    padding: 0;
    margin: 0;
}
.home {
    width: 100%;
    align-items: center;
    font-family: "Arial", sans-serif;
    color: #3e497a;
}

.avatar {
    margin-top: 0px;
}

.avatar-logo {
    width: 300px;
    height: 300px;
    border-radius: 10%;
}

.about {
    width: 100%;
    height: calc(100vh - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #f0f0f0;
    background-color: rgb(19, 19, 72);
}


